






















































































.pagination {
	display: flex;
	align-items: center;
	justify-content: center;
	width: 100%;
	margin-top: 2rem;

	.pagination-button {
		font-size: 0;
		line-height: 0;
	}
}
[disabled] {
	visibility: hidden;
}
.page-number {
	height: 40px;
	padding: 0;
	margin: 0;
	font-family: inherit;
	font-size: 14px;
	font-weight: 500;
	color: #fff;
	text-transform: uppercase;
	background: rgba(255, 255, 255, 0);
	background-color: var(--primaryColor);
	border: none;
	border-radius: 2px;
	box-shadow: 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 2px 2px 0 rgba(0, 0, 0, 0.14),
		0 1px 5px 0 rgba(0, 0, 0, 0.12);
	transition: 0.4s cubic-bezier(0.4, 0, 0.2, 1);
	&:hover,
	&:focus {
		background: var(--accentColor);
	}
	input {
		width: 100%;
		max-width: 88px;
		height: 40px;
		padding: 0;
		margin: 0;
		font-size: 1.2em;
		font-weight: bold;
		line-height: 1em;
		color: inherit;
		text-align: center;
		background: transparent;
		border: none;
		outline: none;
		&::-webkit-inner-spin-button,
		&::-webkit-outer-spin-button {
			margin: 0;
			-webkit-appearance: none;
			-moz-appearance: none;
			appearance: none;
		}
	}
}
