





























































































.overflow-container {
	width: 100%;
	overflow-x: auto;
}
.table {
	box-sizing: border-box;
	// width: 100%;
	width: calc(100% - 2px);
	border-collapse: collapse;
	th {
		padding: 0 4px;
		text-align: left;
		&:last-of-type {
			text-align: right;
		}
	}
	tbody tr {
		&:hover {
			background-color: #ccc;
		}
	}
}

.fade-enter-active,
.fade-leave-active {
	transition: opacity 0.3s;
}
.fade-enter,
.fade-leave-to {
	opacity: 0;
}
