






































































.table-select {
	width: 100%;
	background: transparent;
	border: 0;
	&:focus {
		outline: none;
	}
	&.multiple {
		height: 2em;
		&:focus {
			height: 7em;
		}
	}
	option:checked {
		color: white;
		background: var(--color-primary);
	}
}
