

























textarea {
	display: block;
	width: 100%;
	min-width: 250px;
	height: 100%;
	padding: 0;
	margin: 0;
	line-height: inherit;
	resize: none;
	background: transparent;
	border: none;
	&::-webkit-scrollbar {
		width: 0;
		height: 0;
		background: transparent;
	}
	&:focus {
		height: 150px;
		outline: none;
	}
}
