





























































































.table-cell {
	display: flex;
	flex-wrap: nowrap;
	align-items: center;
	.input {
		flex: 1;
	}
	.active-toggle {
		margin: 0;
	}
}
.invisible {
	opacity: 0;
	&:focus,
	&:focus-within,
	&:hover {
		opacity: 1;
	}
}
input[type="checkbox"][disabled] {
	display: none;
}
