



















































































td {
	position: relative;
	padding: 0 4px;
	overflow: hidden;
	overflow-x: auto;
	white-space: nowrap;
	border: 1px solid #333;
	&:focus-within:not(:last-of-type) {
		box-shadow: inset 0 0 0 2px var(--primaryColor);
	}
}
.divider {
	border-bottom: 1px solid #333;
}
.action {
	padding: 0;
	margin: 0 0.25em;
}
.spacer {
	height: 2rem;
}
